<template>
  <!-- 登录模块 -->
  <div class="flex-box">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>用户密码修改</span>
      </div>
      <div>
        <el-form :rules="rules" ref="user" :model="user" label-width="70px">
          <el-form-item label="久密码" prop="oldpwd">
            <el-input
              v-model="user.oldpwd"
              placeholder="请输入久密码"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item prop="password" label="新密码">
            <el-input
              v-model="user.password"
              placeholder="请输入新密码"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item class="flex-box" label-width="0px">
            <el-button type="primary" @click="submitForm">修改</el-button>
            <el-button @click="resetForm('user')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>
<script>
import tools from '@/js/tools';
export default {
  data() {
    return {
      user: {
        oldpwd: '',
        password: ''
      },
      rules: {
        oldpwd: [
          { required: true, message: '请输入久密码', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    };
  },
  methods: {
    submitForm() {
      let commitPwd = JSON.parse(JSON.stringify(this.user));
      commitPwd.password = tools.md5(commitPwd.password);
      commitPwd.oldpwd = tools.md5(commitPwd.oldpwd);
      tools.ajax('/user/auth/updatePwd', commitPwd, (data) => {
        if (data.success) {
          tools.ajax('/user/auth/logout', {}, (data) => {
            this.$store.dispatch('updatedUser');
            this.$message('密码修改成功请重新登录');
            this.$router.push({
              path: '/user/login'
            });
          });
        }
        this.resetForm('user');
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style socep>
.box-card {
  min-width: 35rem;
  height: 20rem;
}
.box-card {
  min-height: 16rem;
}
.el-form-item {
  margin-bottom: 2rem;
}
.flex-box button {
  margin: 0 1rem;
}
.el-dialog {
  width: 30%;
}
.flex-box {
  height: 100%;
}
</style>
